<template>
  <div class="vertical" style="position:fixed">
       
<div v-for="tag in tags" :key="tag.id">
      <a>   <div class="subheader text-center tag">
    {{ tag.name }}
    </div></a>
</div>


  </div>
</template>

<script>
export default {
    data() {
        return {
            tags: [ ]
        }
    },
    created () {
        
        this.axios.get('category').then(res=>{
            console.log(res);
            
            this.tags=res.data
        })
    },
  
}
</script>

<style>
.tag{
    padding: 10px;
    transition: all .3s
}
.tag:hover{
    background-color:skyblue
}

 

</style>
